*{margin:0;padding:0;font-family:"KIMM","SpaceGrotesk",Apple SD Gothic Neo,돋움,dotum,sans-serif;font-size:inherit;word-break:break-all}html,body{width:100%;height:100%;color:#171717}body{background-color:#f7f8f9}#wrapper{min-height:100vh;background-color:#fff}h1,h2,h3,h4,p{margin:0}ul,ol,li,dl,dt,dd{list-style:none;margin:0;padding:0}button{padding:0;border:0;background-color:rgba(0,0,0,0);cursor:pointer}a{color:inherit;text-decoration:none}#container{position:relative;height:100%;min-height:100vh;padding:0;box-sizing:border-box;background-color:#fff}#container:after{content:"";clear:both;display:block;height:0;visibility:hidden}.no-scroll{height:100%;min-height:100%;overflow:hidden !important;touch-action:none}@media(min-width: 376px){#wrapper{width:375px;margin:0 auto;box-shadow:0px 0px 7px 0px rgba(113,118,130,.4)}}#header{display:block;position:relative;width:100%;height:50px;padding:0 20px;box-sizing:border-box}#header::after{content:"";clear:both;display:block;height:0;visibility:hidden}#header h1{display:none;float:left;margin-left:12px;font-size:18px;font-weight:300;line-height:30px;color:#171717}#header .header-logo{display:block;width:50px;height:30px}#header .header-logo img{display:block;width:auto;height:100%}#header .header-back{float:left;width:30px;height:30px;background-image:url("../images/header_back.png");background-repeat:no-repeat;background-size:auto 10px;background-position:left center}#header .header-back+h1{display:block}#header .header-back~.header-logo{display:none}#header+#container{min-height:calc(100vh - 50px);padding:0 20px 50px}#header{padding:10px 20px}#error{position:absolute;top:50%;left:50%;width:100%;padding:0 30px;box-sizing:border-box;transform:translate(-50%, -50%);text-align:center}#error h1{display:block;width:160px;margin:0 auto 20px;background-color:#fff;font-size:58px;color:#fa7d38}#error strong{display:block;font-size:24px;text-align:center}#error p{margin-top:10px;color:#888}#error .btn{margin-top:20px}#error .zero{position:relative}#error .zero .eye1,#error .zero .eye2{display:block;position:absolute;top:50%;left:50%;transform:translateY(-10px);width:4px;height:7px;border-radius:999px;background-color:#fa7d38}#error .zero .eye1{transform:translate(-7px, -10px)}#error .zero .eye2{transform:translate(3px, -10px)}#error .zero .lips{position:absolute;width:6px;height:4px;border:4px solid #fa7d38;border-bottom:0;border-top-left-radius:7px;border-top-right-radius:7px;top:50%;left:calc(50% - 7px);transform:translateY(-1px)}.intro-btn-group{width:100%;padding:0 40px 50px;box-sizing:border-box}.intro-btn-group .btn+.btn{margin-top:8px}